import React from "react";

import { Link } from "react-router-dom";
import { bhremove } from "../js/scripts";

import logo from "../img/Company-logo.svg";

export default function Header(props) {
  return (
    <>
      <Link className="logo" to="/">
        <img
          src={logo}
          alt="Toy.Stream logo"
          width="135"
          height="75"
          title="Toy.Stream"
        />
      </Link>
      <nav className="header-menu">
        <Link
          className={props.mlink}
          to="/"
          title="Главная"
          onClick={() => {
            bhremove();
          }}
        >
          Главная
        </Link>
        <Link
          className={props.comlink}
          to="/company"
          title="Компания"
          onClick={() => {
            bhremove();
          }}
        >
          Компания
        </Link>
        <Link
          className={props.glink}
          to="/games"
          title="Игры"
          onClick={() => {
            bhremove();
          }}
        >
          Игры
        </Link>
        <Link
          className={props.carlink}
          to="/career"
          title="Карьера"
          onClick={() => {
            bhremove();
          }}
        >
          Карьера
        </Link>
        <Link
          className={props.nlink}
          to="/news"
          title="Новости"
          onClick={() => {
            bhremove();
          }}
        >
          Новости
        </Link>
        <Link
          className={props.conlink}
          to="/contacts"
          title="Контакты"
          onClick={() => {
            bhremove();
          }}
        >
          Контакты
        </Link>
      </nav>
      <Link
        className="btn lang-sw"
        to="/"
        title="Switch language"
      >
        ENG
      </Link>
    </>
  );
}
